






















import Vue from 'vue';
import {Component, Prop} from 'vue-property-decorator';

@Component
export default class Numberpad extends Vue{
  @Prop(Number) value!: number;
  output = this.value.toString();

  inputContent(event: MouseEvent){
    if(this.output.length===16){return}
    const button = (event.target as  HTMLButtonElement).textContent as string;

    if(this.output==='0'){

      if('0123456789'.indexOf(button)>=0){

        this.output = button;
      }else{

        this.output += button;
      }
      return;
    }
    if(this.output.indexOf('.')>=0 && button==="."){
      return
    }
    this.output += button;

  }

  ok(){console.log('hi');
    this.$emit("update:value",parseFloat(this.output));
    this.$emit("update:record",parseFloat(this.output));
    this.output = '0';
  }

  remov(){
    // let leng = this.output.length - 1;
    // this.output = this.output.substr(0,leng);
    // if (this.output.length===0){
    //   this.output='0';
    //   console.log("this.output==='0'");
    // }

    if(this.output.length===1){
      this.output='0';
    }else{
      this.output = this.output.slice(0,-1);
      console.log(this.output.slice(0,-1));
    }
  }

  clear(){
    this.output = '0';
  }
}
